@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0 0 0 105px;
    box-sizing: border-box;
	
	@include ipad-landscape {
		padding-left: 0;
	}

    .eltdf-comment-holder-inner {
        padding: 46px 0 20px;
    }
	
	.eltdf-comment {
		
		&.eltdf-pingback-comment {
			
			.eltdf-comment-text {
				padding: 0;
			}
		}
	}

    .eltdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include eltdfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 29px 0 0;

            .eltdf-comment {
                border-bottom: 1px solid $default-border-color;
            }
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
            border-bottom: 1px solid $default-border-color;

            .eltdf-comment {
                border-bottom: 0;
            }
        }
    }

    .eltdf-comments-title {
        margin-bottom: 40px;

        > * {
            margin: 0;
        }
    }

    .eltdf-comment-image {
        position: relative;
        display: block;
        top: 22px;
        left: 0;
        width: 40px;
        height: 40px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .eltdf-comment-text {
        padding: 0 0 0 78px;
        min-height: 40px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
            margin: 6px 0 4px;

	        .eltdf-comment-name {
	            margin: 0;
		        padding-right: 100px;
	        }

	        .eltdf-comment-date {
		        position: relative;
		        margin: 6px 14px 0;
		        font-family: $default-text-font;
		        font-size: 13px;
		        text-transform: none;
		        vertical-align: baseline;
		        color: $default-text-color;

	            &:before {
	                content: '/';
	                position: relative;
	                left: -6px;
	            }
	        }
        }

	    .eltdf-comment-btns {
		    position: absolute;
		    top: 0;
		    right: 0;

		    @include phone-portrait {
			    position: relative;
			    display: inline-block;
		    }
	    }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin: 10px 0 0 10px;
            font-size: 13px;
            color: $default-text-color;
            letter-spacing: 0.05em;
            text-transform: capitalize;

            i {
                font-size: 14px;
            }

	        &:last-child {
		        margin-left: 0;
	        }

            &:hover {
                color: $default-heading-color;
            }
        }

        .eltdf-text-holder p {
            margin: 10px 0 60px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltdf-comment-form {
    padding: 0 0 0 105px;
    clear: both;
    box-sizing: border-box;
    
    .eltdf-comment-form-inner {
        padding: 25px 0 60px;
    }
    
    .comment-reply-title {
        margin: 0 0 37px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {

        padding: 0;
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }

    .eltdf-comment-form {
        padding: 0;
    }
}

@include phone-landscape {
	
    .eltdf-comment-holder {

        padding: 0;
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }
    }

    .eltdf-comment-form {
        padding: 0;
    }
}

@include phone-portrait {
	
    .eltdf-comment-holder {

        padding: 0;

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }

    .eltdf-comment-form {
        padding: 0;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.eltdf-style-form textarea,
#respond input[type='text'],
#respond input[type='email'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

#respond input[type='text'],
#respond input[type='email'] {
    border: 0;
    border-bottom: 1px solid $default-heading-color;
    padding: 11px 22px;
}

.post-password-form input[type='password'] {
	display: block;
	max-width: 540px;
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include eltdfButtonDefaultStyle();
	@include eltdfButtonSize();
	@include eltdfButtonOutlineColor();
	border-radius: 0;
	cursor: pointer;
	
	&:hover {
		@include eltdfButtonOutlineHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 15px 0 0;
}

#respond textarea,
.eltdf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
}

.eltdf-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Custom CF7 Form - start
========================================================================== */
.eltdf-custom-form-holder {
    .eltdf-input-name-holder,
    .eltdf-input-email-holder {
        float: left;
        width: 50%;
    }

    .eltdf-input-name-holder {
        input {
            border-right: 0;
        }
    }

    .eltdf-input-subject-holder {
	    clear: both;
        input {
            border-top: 0;
            border-bottom: 0;
        }
    }

    .eltdf-input-message-holder {
        textarea {
            border-bottom: 0;
        }
    }

    input,
    textarea {
        margin: 0 !important;
    } 

    .eltdf-custom-form-submit {
        input.wpcf7-form-control.wpcf7-submit  {
            width: 100%;
            padding: 18px 46px;
        }
    }
}

/* ==========================================================================
   #Custom CF7 Form - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.eltdf-slider-is-moving {
		
		.owl-item {
			
			.eltdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.eltdf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include eltdfTransform(translateY($pos));
            }
        }
    }

    &.eltdf-slider-hide-pagination .owl-dots  {
        display: none;
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			padding: 0;
			margin: 0;
			color: $default-text-color;
			background: none;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;
			z-index: 3;
			@include eltdfTransform(translateY(-50%));
			@include eltdfTransition(color .2s ease-out);
			
			&:hover {
				color: $default-heading-color;
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 15px;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}

			.eltdf-prev-text,
			.eltdf-next-text {
				font-family: $default-text-font;
			}
		}
		
		.owl-prev {
			left: 30px;
		}
		
		.owl-next {
			right: 30px;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 18px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			padding: 0;
			margin: 0 7px;
			background: none;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 6px;
				height: 6px;
				border-radius: 3em;
				background-color: #bcbab2;
				border: 1px solid #bcbab2;
				box-sizing: border-box;
				@include eltdfTransition(background-color .2s ease-out, border .2s ease-out);
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: $default-heading-color;
					border-color: $default-heading-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .eltdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .eltdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;
                @include eltdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.eltdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .eltdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .eltdf-wrapper {
        padding: 0 !important;
    }
    
    .eltdf-content {
	    height: 100vh;
    }
	
	&.admin-bar {
		
		.eltdf-content {
			height: calc(100vh - 32px);
		}
	}

    .eltdf-content-inner {
	    @include eltdfTableLayout();
    }

    .eltdf-page-not-found {
	    @include eltdfTableCellLayout();
        padding: 0 20%;
	    text-align: center;

        .eltdf-404-title {
            margin: 0;
        }
	
	    .eltdf-404-subtitle {
            margin: 35px 0 0;
        }
	    
	    .eltdf-404-text {
		    margin: 10px 0 0;
	    }

        .eltdf-btn {
            margin: 30px 0 0;
	
	        &.eltdf-btn-light-style {
		        color: #585858;
		        background-color: #fff;
		        border-color: #fff;
		
		        &:hover {
			        color: #fff !important;
			        background-color: transparent !important;
			        border-color: #fff !important;
		        }
	        }
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
    position: fixed;
    width: 190px;
	right: -60px;
	bottom: 108px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;
	@include eltdfTransform(rotate(-90deg));
	
	&:hover {
		
		&:after {
			width: 100%;
		}
	}
	
	&:after {
		
		position: absolute;
		bottom: 0;
		left: 0;
		content: '';
		width: 40%;
		height: 1px;
		background-color: $default-heading-color;
		transition: width .8s ease;
	}

    &.off {
        opacity: 0;
        visibility: hidden;
        @include eltdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include eltdfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: inline-block;
	    vertical-align: top;
        width: 80px;
        height: 26px;
	    font-family: $default-heading-font;
        font-size: 12px;
	    line-height: 1em;
	    letter-spacing: .1em;
        text-align: center;
        color: $default-heading-color;
        overflow: hidden;
	    backface-visibility: hidden;
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.eltdf-self-hosted-video-holder {
	
    .eltdf-video-wrap {
	
	    .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */
    /* ==========================================================================
   #Custom Font - start
   ========================================================================== */
    @font-face {
        font-family: 'Bebas';
        src: url('../fonts/BEBAS.ttf')  format('truetype');
    }
/* ==========================================================================
   #Custom Font - end
   ========================================================================== */

.eltdf-smooth-transition-loader {
	
	visibility: visible;
	@include eltdfTransform(translate3d(0,0,0));
	@include eltdfTransition(all 1s cubic-bezier(.86,0,.07,1));
	
	&.eltdf-remove {
		visibility: hidden;
		@include eltdfTransform(translate3d(101%,0,0));
	}
}

