/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.eltdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {

        &:last-child {
            margin: 0 !important;
        }
	}
}

.wpb_widgetised_column {
    position: relative;

    .widget {
	
	    &:last-child {
		    margin: 0 !important;
	    }
    }
}
/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
	margin: 0 0 33px;
	
	.eltdf-widget-title {
		margin: 0 0 5px;
		
		a {
			color: inherit;
		}
	}
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}

    /*
    *   Specific styles
    */
    &.widget_text {

        img {
            display: block;
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        .eltdf-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_search {

        form {
            @include eltdfRelativeHolderLayout();
            clear: both;

            > div {
                @include eltdfRelativeHolderLayout();
                border: 0;
                border-bottom: 1px solid $default-heading-color;
                box-sizing: border-box;
            }
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            height: 52px;
            font-family: inherit;
            font-weight: 400;
            font-size: 12px;
            line-height: 36px;
            color: $default-heading-color;
            text-transform: uppercase;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include eltdfTransition(color .2s ease-out);
        }

        input {
            width: calc(100% - 50px);
            padding: 0;
            border-right: 1px solid $default-heading-color;
            text-align: center;
            font-family: $default-heading-font;
            letter-spacing: 0.1em;
	
	        @include eltdfPlaceholder {
		        color: inherit;
	        }
        }

        button {
            width: 50px;
            padding: 10px 0;
            font-size: 15px;
            color: $default-text-color;
            cursor: pointer;
            @include eltdfTransition(color .2s ease-in-out);

            &:hover {
                color: $default-heading-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {

        ul {

            li {
                margin: 0;
                padding: 4px 0;
                font-size: 13px;
                letter-spacing: 0.05em;

                &:last-child {
                    border: none;
                }

                ul {
                    margin: 4px 0 0;
                    padding: 0 0 0 10px;

                    li {

                        &:last-child {
                            padding: 4px 0 0;
                        }
                    }
                }
            }
        }
    }

    &.eltdf-blog-list-widget {
        .eltdf-widget-title {
            margin: 0 0 19px;
        }
    }

    &.widget_recent_entries {
	
	    .eltdf-widget-title {
            margin: 0 0 23px;
        }

        ul {

            li {
                margin: 0 0 12px;

                > span {
                    display: block;
	                font-size: 14px;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
            max-width: 100%;
            height: 54px;
            padding: 0 18px;
            line-height: 54px;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            font-family: inherit;
            font-size: inherit;
            font-style: inherit;
            font-weight: inherit;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {
            
            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {

        .eltdf-widget-title {
            margin: 0 0 9px;
        }

        a {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            font-family: inherit;
            font-weight: inherit;
            font-size: 13px !important;
            background-color: transparent;

            &:after {
                content: ',';
                color: $default-text-color;
                margin: 0;
            }

            &:last-child {

                &:after {
                    display: none;
                }
            }

            &:hover {
                color: $default-heading-color;
            }
        }
    }
    
    &.widget_media_image {
        margin-bottom: 20px;
        
        a, img {
            display: block;
        }
    }
}

.vc_wp_search {

    .widget {

        &.widget_search {

            form {

                > div {
                    background-color: #fff;
                    border: none;
                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;
            }
        }
    }
}

.eltdf-page-header,
.eltdf-top-bar,
.eltdf-sticky-header,
.eltdf-mobile-header {
    
    .widget {
        margin: 0;
        
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }
    }
}

.eltdf-side-menu {
    
    .widget {
        margin-bottom: 5px;
    }
    
    .eltdf-icon-widget-holder {
        color: #fff;
        
        &:hover {
            color: #878787;
        }
    }
}

.eltdf-side-menu,
.eltdf-page-footer {

    .widget {

        &:last-child {
            margin: 0 !important;
        }

        h1, h2, h3, h4, h5, h6 {
            margin: 0;
        }

        .eltdf-widget-title {
            margin: 0 0 15px;
        }
    
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_comments,
        &.widget_nav_menu {
        
            ul {
            
                li {
                    font-style: inherit;
                }
            }
        }
    
        &.widget_tag_cloud {
    
            a {
                font-style: inherit;
            }
        }
    }
}

.eltdf-page-footer {

    .widget {

        table thead tr,
        table tbody tr {
            border-color: $default-border-color;
        }

        table tbody th {
            border-right-color: $default-border-color;
        }

        /*
        *   Specific styles
        */
	    #wp-calendar {
		
		    caption {
			    color: $default-text-color;
		    }
	    }
	
	    &.widget_rss {
		
		    .eltdf-footer-widget-title {
			
			    .rsswidget {
				    color: $default-text-color;
				
				    &:hover {
					    color: $default-text-color;
				    }
			    }
		    }
	    }
	    
        &.widget_search {

            form {

                > div {
                    border: 1px solid $default-border-color;
                }
            }

            input {
                color: $default-text-color;
	            border-color: $default-border-color;
             
	            @include eltdfPlaceholder {
		            color: inherit;
	            }

                &[type='submit'] {
                    color: $default-text-color;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: $default-text-color;
                border-color: $default-border-color;

                option {
                    background-color: #fefcf0;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: $default-text-color;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: $default-text-color;
                }
            }
        }

        &.eltdf-google-map-widget {
            .eltdf-widget-title {
                margin: 0 0 22px;
            }
        }
    }
}

.eltdf-side-menu {

    .widget {

        .eltdf-widget-title {
		    color: $default-text-color;
	    }

        table thead tr,
        table tbody tr {
            border-color: $default-text-color;
        }

        table tbody th {
            border-right-color: $default-text-color;
        }

        a {
            color: $default-text-color;

            &:hover {
                color: #fff;
            }
        }

        /*
        *   Specific styles
        */

        #wp-calendar {

            caption {
                color: $default-text-color;
            }

            td#today {
                color: #000;
                background-color: #fff;
            }
        }

        &.widget_rss {

            .eltdf-widget-title {

                .rsswidget {

                    &:hover {
                        color: #fff;
                    }
                }
            }
        }

        &.widget_search {

            form {

                > div {
                    border-bottom: 1px solid $default-text-color;
                }
            }

            input,
            button {
                color: $default-text-color;
            }

            input {
                border-right: 1px solid $default-text-color;
            }

            button {
                color: $default-text-color;

                &:hover {
                    color: #fff;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                background-color: #0b0b0b;
                border: 1px solid $default-text-color;
            }
        }
    }

    .eltdf-icon-widget-holder {

        .eltdf-icon-element,
        .eltdf-icon-text {
            display: block;
        }

        .eltdf-icon-text {
            margin: 21px 0 0;
            padding: 0;
        }

        &:hover {
            color: #fff;
        }
    }
}

.eltdf-top-bar {

	a {
		color: #969696;

		&:hover {
			color: $first-main-color;
		}
	}

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        &.widget_text {
            margin: 0 8px 0 0;
        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }
    }
}

.widget {
    
    &.eltdf-opening-hours-widget {
        margin: 0 0 15px;

        .eltdf-page-footer & {
            max-width: 77%;
        }
    }
} 
/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */